*, *:before, *:after {
  box-sizing: border-box;
}

html, body, div, span, object, iframe, figure, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, code, em, img, small, strike, strong, sub, sup, tt, b, u, i, ol, ul, li, fieldset, form, label, table, caption, tbody, tfoot, thead, tr, th, td, main, canvas, embed, footer, header, nav, section, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  text-size-adjust: none;
}

footer, header, nav, section, main {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

input {
  -webkit-appearance: none;
  border-radius: 0;
}

.d--flex {
  display: flex;
}
.d--block {
  display: block;
}
.d--none {
  display: none;
}

.a--center {
  align-items: center;
}

.j--center {
  justify-content: center;
}

.f--nowrap {
  flex-wrap: nowrap;
  overflow-x: scroll !important;
  overflow-y: hidden;
}
.f--grow {
  flex-grow: 1;
}
.f--col {
  flex-direction: column;
}

.w--100 {
  width: 100%;
}

.h--100 {
  height: 100%;
}

.p--rel {
  position: relative;
}

.scroll--y {
  overflow-y: scroll;
}
.scroll--x {
  overflow-x: scroll;
}

.t--700 {
  font-weight: 700;
}
.t--blue {
  color: var(--color-blue-porto);
}
.t--ellipsis {
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  width: 100%;
  vertical-align: middle;
}

/* Ionic Variables and Theming. For more info, please see:
http://ionicframework.com/docs/theming/ */
/** Ionic CSS Variables **/
:root {
  --ion-font-family: -apple-system, BlinkMacSystemFont, "Helvetica Neue", "Roboto", sans-serif;
  --ion-color-primary: #1577eb;
  --ion-color-primary-rgb: 21, 119, 235;
  --ion-color-primary-contrast: #ffffff;
  --ion-color-primary-contrast-rgb: 255, 255, 255;
  --ion-color-primary-shade: #1269cf;
  --ion-color-primary-tint: #2c85ed;
  --ion-color-secondary: #1b2229;
  --ion-color-secondary-rgb: 27, 34, 41;
  --ion-color-secondary-contrast: #ffffff;
  --ion-color-secondary-contrast-rgb: 255, 255, 255;
  --ion-color-secondary-shade: #181e24;
  --ion-color-secondary-tint: #32383e;
  --ion-color-tertiary: #1577eb;
  --ion-color-tertiary-rgb: 21, 119, 235;
  --ion-color-tertiary-contrast: #ffffff;
  --ion-color-tertiary-contrast-rgb: 255, 255, 255;
  --ion-color-tertiary-shade: #1269cf;
  --ion-color-tertiary-tint: #2c85ed;
  --ion-color-success: #34c759;
  --ion-color-success-rgb: 52, 199, 89;
  --ion-color-success-contrast: #000000;
  --ion-color-success-contrast-rgb: 0, 0, 0;
  --ion-color-success-shade: #2eaf4e;
  --ion-color-success-tint: #48cd6a;
  --ion-color-warning: #ffd60a;
  --ion-color-warning-rgb: 255, 214, 10;
  --ion-color-warning-contrast: #000000;
  --ion-color-warning-contrast-rgb: 0, 0, 0;
  --ion-color-warning-shade: #e0bc09;
  --ion-color-warning-tint: #ffda23;
  --ion-color-danger: #f20740;
  --ion-color-danger-rgb: 242, 7, 64;
  --ion-color-danger-contrast: #ffffff;
  --ion-color-danger-contrast-rgb: 255, 255, 255;
  --ion-color-danger-shade: #d50638;
  --ion-color-danger-tint: #f32053;
  --ion-color-medium: #d3d7db;
  --ion-color-medium-rgb: 211, 215, 219;
  --ion-color-medium-contrast: #000000;
  --ion-color-medium-contrast-rgb: 0, 0, 0;
  --ion-color-medium-shade: #babdc1;
  --ion-color-medium-tint: #d7dbdf;
  --ion-color-light: #f8fafd;
  --ion-color-light-rgb: 248, 250, 253;
  --ion-color-light-contrast: #000000;
  --ion-color-light-contrast-rgb: 0, 0, 0;
  --ion-color-light-shade: #dadcdf;
  --ion-color-light-tint: #f9fbfd;
}

ion-content,
ion-refresher-content,
ion-refresher,
.refresher-pulling,
.refresher-pulling {
  --ion-background-color: var(--color-blue-ice);
}

ion-card {
  --color: var(--color-grey-graphite);
  --ion-background-color: #fff;
}

ion-icon {
  color: var(--color-blue-porto);
}

.side-menu {
  background-color: var(--ion-color-medium);
}

.ion-color-deliverect {
  --ion-color-base: var(--ion-color-deliverect);
  --ion-color-base-rgb: var(--ion-color-deliverect-rgb);
  --ion-color-contrast: var(--ion-color-deliverect-contrast);
  --ion-color-contrast-rgb: var(--ion-color-deliverect-contrast-rgb);
  --ion-color-shade: var(--ion-color-deliverect-shade);
  --ion-color-tint: var(--ion-color-deliverect-tint);
}

.ion-color-flipdish {
  --ion-color-base: var(--ion-color-flipdish);
  --ion-color-base-rgb: var(--ion-color-flipdish-rgb);
  --ion-color-contrast: var(--ion-color-flipdish-contrast);
  --ion-color-contrast-rgb: var(--ion-color-flipdish-contrast-rgb);
  --ion-color-shade: var(--ion-color-flipdish-shade);
  --ion-color-tint: var(--ion-color-flipdish-tint);
}

.alert {
  padding: 20px 10px;
  border: 1px solid var(--ion-color-danger);
  color: var(--ion-color-danger);
  background: #ffe6e6;
  border-radius: 4px;
  margin-inline: 10px;
  font-size: 14px;
}